import PropTypes from "prop-types";
import { Container, Row, Col } from "react-bootstrap";
import ServicesItem from "../../components/services";
import {
    CCarousel,
    CCarouselItem,
    CImage,
    CCarouselCaption,
} from "@coreui/react";
import { StaticImage } from "gatsby-plugin-image";
import datas from "../../data//services-content/data.json";
import { ServicesSection } from "./style";
const ServicsArea = ({ data }) => {
    return (
        <ServicesSection>
            <Container>
                <div className="inner-wrapper">
                    <h1>{datas.title}</h1>
                    <p>{datas.description}</p>
                </div>

                <Row className="gx-5">
                    {data?.items &&
                        data?.items.map((item, i) => {
                            return (
                                <Col lg={6} md={6} key={i}>
                                    <ServicesItem
                                        iconImage={item.images[0].src}
                                        lineImage={item.images[1].src}
                                        title={item.title}
                                        disText={item.excerpt}
                                        actionClass={item.className}
                                        outlineColor={item.outlineColor}
                                        slug={item.slug}
                                    />
                                </Col>
                            );
                        })}
                </Row>
                <div className="package-section api-integration-section container">
                    <div className=" d-flex flex-wrap flex-row ">
                        <div className="api-types-accordion services-inner-wrapper">
                            <div className="api-types-title-wrapper services-page-wrapper">
                                <h5>{datas.subtitle}</h5>
                                <h4 className="seo-page-title">
                                    {datas.section2title}
                                </h4>
                                <p>{datas.section2description}</p>
                            </div>
                            <ul>
                                {datas.items.map((itm, i) => {
                                    return (
                                        <li key={i.id}>
                                            <i className={itm.class}></i>
                                            {itm.item}
                                        </li>
                                    );
                                })}
                            </ul>
                            <div>
                                <p>{datas.bottomdescription}</p>
                            </div>
                        </div>
                        <div className="api-types-title"></div>
                    </div>
                </div>

                <div className="others-think-section">
                    <h2>{datas.section3title}</h2>
                    <p>{datas.section3description}</p>

                    <div className="slider-section container">
                        <div className="container">
                            <CCarousel controls indicators>
                                {datas.slider.map((itm, i) => {
                                    return (
                                        <CCarouselItem>
                                            <CCarouselCaption className="d-none d-md-block">
                                                <i className={itm.symbol}>
                                                    {itm.rating}
                                                </i>

                                                <p>{itm.description}</p>
                                                <StaticImage
                                                    src="../../data/images/icons/3.png"
                                                    alt="logo_not_found"
                                                />
                                                <h5>{itm.Name}</h5>
                                            </CCarouselCaption>
                                        </CCarouselItem>
                                    );
                                })}
                            </CCarousel>
                        </div>
                    </div>
                </div>
            </Container>
        </ServicesSection>
    );
};
ServicsArea.propTypes = {
    data: PropTypes.shape({
        items: PropTypes.arrayOf(
            PropTypes.shape({
                id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
                fields: PropTypes.shape({
                    slug: PropTypes.string,
                }),
                alt: PropTypes.string,
            })
        ),
    }),
};
export default ServicsArea;
